var TEAMMEMBER=[{id:0,name:'Orion Culbertson',image:'assets/images/orionSelfie.png',country:'USA',description:'Hey! I am Orion. I am a senior at San Francisco State University. I love art, dogs, hikes, programming, and lots of other things. I love German Shepherds, but all dogs are good dogs. I am the Front End Lead for this project, and my goals are to get comfy in this area as well as learn a lot about the other parts of the project.',source:'N/A'},{id:1,name:'Grayson Dew',image:'assets/images/grayson.png',country:'USA',description:'My name is Grayson Dew, and I am a senior at SFSU. I attended and transferred from Leeward Community College, on Oahu, after 6 years in the Navy working as a linguist. I have spent most of my programming time with C++, C#, C, Java, and a bit of Javascript. At the moment I am learning Python, Swift, and Typescript. There is so much potential with this degree that I have not yet figured out what I want to do with it once I have graduated. I am going to send an application to pretty much every organization and see what offers I have. I am a sucker for escape rooms and try to do any that I come across.',source:'N/A'},{id:2,name:'Kris P',image:'assets/images/krisP.png',country:'USA',description:'Hi there! My name is Kris P and I love dogs and machine learning. I am in my last semester at SFSU, and I will graduate with a degree in Computer Science in December. My favorite breed of dog is West Highland Terrier, and I greatly look forward to taking lots of pictures of all sorts of dogs to test out the software we build in this project. My programming experence is mostly with C++ and Java, but I have developed projects with JavaScript as well. I am currently studying machine learning techniques using Python and a broad array of different libraries for Python. I hope this site serves as an educational tool that is both fun and engaging for users from all walks of life!',source:'N/A'},{id:3,name:'Miguel Galvan',image:'assets/images/miguel.png',country:'USA',description:'Hey you all! Excited to combine two things that interest me: dogs and machine learning! I am a senior at SFSU with knowledge of Java, C++, Python, MATLAB, JavaScript, CSS, and HTML. My favorite dogs are beagles, they have the best barks!',source:'N/A'},{id:4,name:'Rigo Perez',image:'assets/images/rigpa268x401.png',country:'Mexico',description:'I am a senior at the San Francisco State University. By the end of the Fall term, I will have accomplished a Bachelor’s Degree in CSC Before I continued my career at San Francisco State, I accomplished a certificate in The Humanities and a CSC Associate Degree for Transferat Foothill Community College, located in Los Altos CA. Throughout my career, I have been exposed to compute subjects like: Operating Systems, Computer Architecture, Web Development, and Embedded Systems (e.g. computers in cars), among others. I have some experience in programing in C, C++, Java, JavaScript, and currently I am learning to code in Python and TypeScript.',source:'Pending'}];

